





















import { Component, Mixins } from 'vue-property-decorator'

import PopupMixin from '@/mixins/popup'

@Component({
  components: {}
})
export default class PopupConfirm extends Mixins(PopupMixin) {
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-confirm-buyer-table',
      isOpen: false
    })
  }

  handleSubmit(): void {
    this.$emit('delete')
    this.handleCancel()
  }
}
